module.exports = "/* ~~~~~~~ INIT. BTN ~~~~~~~ */\n:host {\n  --def: white;\n  --inv: #333; }\n.btn:not(.disabled) {\n  border: 1px solid var(--inv);\n  position: relative;\n  color: var(--inv);\n  font-weight: 700;\n  text-transform: uppercase;\n  transition: all 600ms cubic-bezier(0.77, 0, 0.175, 1);\n  cursor: pointer;\n  -webkit-user-select: none;\n     -moz-user-select: none;\n      -ms-user-select: none;\n          user-select: none; }\n.btn-sm {\n  font-size: 12px;\n  padding: 8px 20px; }\n.btn-md {\n  font-size: 14px;\n  padding: 10px 25px; }\n.btn-lg {\n  font-size: 16px;\n  padding: 20px 30px; }\n.btn:before, .btn:after {\n  content: '';\n  position: absolute;\n  transition: inherit;\n  z-index: -1; }\n.btn:not(.disabled):hover {\n  color: var(--def);\n  transition-delay: .6s; }\n.btn:not(.disabled):hover a:hover {\n    color: var(--def); }\n.btn:not(.disabled):hover:before {\n  transition-delay: 0s;\n  left: 0;\n  width: 100%; }\n.btn:not(.disabled):hover:after {\n  background: var(--inv);\n  transition-delay: .4s;\n  top: 0;\n  height: 100%; }\n/* From Middle */\n.btn:not(.disabled):before {\n  top: 0;\n  left: 50%;\n  height: 100%;\n  width: 0;\n  border: 1px solid var(--inv);\n  border-left: 0;\n  border-right: 0; }\n.btn:not(.disabled):after {\n  bottom: 0;\n  left: 0;\n  height: 0;\n  width: 100%;\n  background: var(--inv); }\n\n/*# sourceMappingURL=data:application/json;base64, */"